export default {
	data() {
		return {
			ph:"",
			pw:"",
			
		}
	},
	onLoad() {

	},
	methods: {
		phone: function(event) {
			this.ph = event.target.value;
		},
		password: function(event) {
			this.pw = event.target.value;
		},
		LOGIN(){
			uni.showLoading({
				title:"登录中..."
			})
			var that = this;
			if(!this.ph){
				uni.showToast({title:"请输入手机号", icon:"none"});
			}else if(!this.pw){
				uni.showToast({title:"请输入密码", icon:"none"});
			}else{
				uni.request({
					url: 'http://106.13.239.202/index.php?c=login&m=login',
					method: 'POST',
					header: {"content-type": "application/x-www-form-urlencoded"}, 
					data: {
						phone:that.ph,
						password:that.pw,
						
					},
					success: res => {
						uni.hideLoading()
						console.log(res);
						if(res.data.status == 'ok'){
							uni.showToast({title:"登录成功", icon:"none"});
							uni.setStorage({
							    key: 'code',
							    data: res.data.code
							});
							uni.setStorage({
							    key: 'phone',
							    data: that.ph
							});
							setTimeout(function () {
								uni.navigateBack({
									
								
								});
							}, 1000);
							
						}else{
							uni.showToast({title:"登录失败", icon:"none"});
		
						}
							
					},
					fail: () => {
					
					},
					complete: () => {}
				});
			}
			
			
		},
		
		
		gotoRegistration() {
			uni.navigateTo({
				url: '/pages/login/registration'
			});
		},
		gotoForgetPassword() {
			uni.navigateTo({
				url: '/pages/login/forget-password'
			});
		}
	}
}
